.label {
  display: inline-flex;
  align-items: center;
  color: var(--blue-400);
}

.label > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.checkbox {
  display: grid;
  place-items: center;
  margin-right: 10px;
  width: 1.4rem;
  height: 1.4rem;
  border-radius: 3px;
  border: 2px solid currentColor;
  cursor: pointer;
}

.checkbox > svg {
  fill: currentColor;
  width: 75%;
  height: 75%;
}

/* reduce specificity with :where() pseudo-class */
input:not(:checked) + :where(.checkbox) > svg {
  visibility: hidden;
}

input:focus + :where(.checkbox) {
  border-color: var(--blue-500);
}

input:disabled ~ :is(.checkbox, .spanClassName) {
  background-color: transparent;
  filter: grayscale(65%) opacity(0.65);
  cursor: unset;
}

.checkbox:hover {
  background-color: var(--blue-200);
}

.checkbox:active {
  background-color: var(--blue-300);
}

.spanClassName {
  user-select: none;
  cursor: pointer;
}
